<template>
  <div>
    <navDischarge/>
    <div class="container bg-light">
      <h1 class="p-5 text-dark text-center">
        <b>Listado de Altas</b>
      </h1>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
              <b-input-group-append>
                <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- Seleccione --</option>
              </b-form-select>
              <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        id="table-discharge"
        show-empty
        hover
        small
        stacked="xl"
        fixed
        empty-filtered-text="No se encontrarón registros en su búsqueda"
        empty-text="No hay datos para mostrar"
        :items="discharges"
        :fields="columns"
        :filter="filter"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        :current-page="currentPage"
        :per-page="10"
        @filtered="onFiltered"
      >
        <template v-slot:cell(paciente)="data">{{data.item.citas.paciente.nombre}} {{data.item.citas.paciente.apellido}}</template>
        
        <template v-slot:cell(medico)="data">{{data.item.citas.medico.nombre}} {{data.item.citas.medico.apellido}}</template>
        
        <template v-slot:cell(fechaSalida)="data">{{convertDateToString(data.item.fechaSalida)}}</template>
        
        <template v-slot:cell(estado)="data">
          <span class="btn btn-danger">{{data.item.citas.estadoCita.descripcion}}</span>
        </template>
      </b-table>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-pagination
            size="md"
            :total-rows="totalRows"
            v-model="currentPage"
            :per-page="10"
            @input="find(currentPage)"
          ></b-pagination>
        </b-col>
      </b-row>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL } from "@/variables.js";
import axios from "axios";
import navDischarge from "@/components/discharge/navDischarge.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "altas",
  data() {
    return {
      columns: [
        {
          key: "paciente",
          label: "Paciente",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "medico",
          label: "Medico",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "abono"
        },
        {
          key: "fechaSalida",
          label: "Fecha de Salida"
        },
        {
          key: "estado"
        }
      ],
      discharges: [],
      discharge:[],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      totalRows:null,
      currentPage:1
    };
  },
  methods: {
    async findAll() {
      axios
        .get(`${URL}/api/altas`)
        .then(response => (this.discharges = response.data))
        .catch(error => console.log(error));
    },
    async find(currentPage) {
      axios
        .get(`${URL}/api/alta?page=${currentPage-1}`)
        .then(response => {
          this.discharge = response.data.content
          this.totalRows=response.data.totalElements
        })
        .catch(error => console.log(error));
    },
    convertDateToString(date) {
      let dateObject = new Date(date);
      let formatter = new Intl.DateTimeFormat("es", {
        year: "numeric",
        month: "long",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
        second: "numeric"
      });
      return formatter.format(dateObject);
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    }
  },
  mounted(currentPage){
    this.find(currentPage)
    this.findAll()
  },
  components: {
    navDischarge,
    Footer
  }
};
</script>

